@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Ubuntu:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
    margin: 0;
    background-color: aliceblue
}
* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
#root {
  background-color: #f0f0f0;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  /* height: 100%; */
}
.__main {
  max-width: 90%;
  margin: auto;
  background-color: #fff;
  min-height: 80vh;
  width: 100%;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  padding-left: 0;
}

.btn {
  background-color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
  width: 230px;
  height: 47px;
  line-height: 47px;
  border-radius: 5px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.02);
  display: flex;
  padding: 0;
  transition: all 0.3s ease-in-out;
}
.btn:hover {
  background-color: #4664ff;
  color: #fff;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}
.btn i {
  flex: 0.2 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 18px;
  border-right: 1px solid #ebe7fb;
}
.btn span {
  flex: 0.8 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #6c84ff;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #3b5bfe;
}

